import React, { useEffect } from "react";
import { Button, Select } from "antd";
import { Link } from "react-router-dom";
import AdSense from "react-adsense";

const { Option } = Select;

const children = [];
const mbtiArr = [
  "ESTJ",
  "ESTP",
  "ESFJ",
  "ESFP",
  "ENTJ",
  "ENTP",
  "ENFJ",
  "ENFP",
  "ISTJ",
  "ISTP",
  "ISFJ",
  "ISFP",
  "INTJ",
  "INTP",
  "INFJ",
  "INFP",
];
for (let i = 0; i < 16; i++) {
  children.push(<Option key={mbtiArr[i]}>{mbtiArr[i]}</Option>);
}
export const Home = () => {
  const handleChange = (value) => {
    localStorage.setItem("my-mbti", value);
  };
  useEffect(() => {
    localStorage.setItem("my-mbti", "ESTJ");
  }, []);
  return (
    <div className="flex justify-center flex-col pt-16">
      <h1 className="text-3xl text-center">MBTI 이상형 테스트</h1>
      <div className="text-center mb-2">
        내가 좋아하는 상대방의 MBTI는 무엇일까?
      </div>
      <div className="text-center mb-2">
        본인의 MBTI 유형을 선택하여 주세요.
      </div>
      <div className="text-center mb-2">
        <Select
          size="large"
          defaultValue="ESTJ"
          onChange={handleChange}
          style={{ width: 336 }}
        >
          {children}
        </Select>
      </div>
      <AdSense.Google
        style={{ display: "block" }}
        client="ca-pub-9130836798889522"
        slot="7740004665"
        format="auto"
        responsive="true"
      />
      <div className="text-center mt-2">
        <Button style={{ width: 336 }} type="primary" size="large">
          <Link to="/page1">START</Link>
        </Button>
      </div>
    </div>
  );
};
